<template>
  <main>
    <el-tabs type="border-card" @tab-click="onTabClick" v-model="activeName">
      <el-tab-pane label="明细结算" name="tab1">
        <detail-bill ref="detailBill"></detail-bill>
      </el-tab-pane>
      <el-tab-pane label="月汇总" name="tab2">
        <month-summary @goto="goto"></month-summary>
      </el-tab-pane>
    </el-tabs>
  </main>
</template>

<script>
  import detailBill from './tab/detailBill';
  import monthSummary from './tab/monthSummary';
  export default {
    name: "returnTransferInterceptFee",
    components: {
      detailBill,
      monthSummary
    },
    data (){
      return {
        activeName: 'tab1'
      }
    },
    methods: {
      onTabClick (){
        this.$nextTick(() => {
          this.z_method.refreshSpread()
        })
      },
      goto(name, row){
        this.activeName = name;
        this.z_method.refreshSpread();
        this.$refs.detailBill.summaryToDetail(row)
      }
    }
  }
</script>

