                    :clearable="false"
                    ref="timePickerB">
                  </el-date-picker>
                </div>
              </li>
              <li>
                <span class="label">费用类型</span>
                <el-select
                  v-model="queryContent.fineType"
                  placeholder=""
                  class="short-input"
                  ref="feeType">
                  <el-option
                    v-for="item in feeType"
                    :key="item.id"
                    :label="item.label"
                    :value="item.id">
                  </el-option>
                </el-select>
              </li>
              <li>
                <span class="label">账单类型</span>
                <el-select
                  v-model="queryContent.settlementType"
                  placeholder=""
                  class="short-input"
                  ref="billType">
                  <el-option
                    v-for="item in billType"
                    :key="item.id"
                    :label="item.label"
                    :value="item.id">
                  </el-option>
                </el-select>
              </li>
            </ul>
            <ul class="search-input-box">
              <li>
                <span class="special-label">结算对象</span>
                <sc-users
                  type="7"
