                  ref="refUser"
                  v-model="queryContent.recManId">
                </sc-users>
              </li>
              <li>
                <span class="label">客户</span>
                <sc-users
                  placeholder=""
                  type="3"
                  class="short-input"
                  ref="refCustomer"
                  v-model="queryContent.customerId">
                </sc-users>
              </li>
              <li>
                <span class="label">运单发放</span>
                <sc-users
                  v-model="queryContent.salesManId"
                  placeholder=""
                  class="short-input"
                  ref="refUserOrCustomer"
                  type="4"/>
              </li>
            </ul>
            <ul class="search-input-box">
              <li>
                <span class="special-label">扣款状态</span>
                <el-select
                  v-model="queryContent.deductStatus"
                  class="short-input"
                  placeholder=""
                  ref="deductStatus">
                  <el-option
                    v-for="item in deductStatus"
                    :key="item.id"
                    :label="item.label"
                    :value="item.id">
                  </el-option>
                </el-select>
              </li>
              <li>
                <span class="label" style="width: 89px">扣款人</span>
                <sc-users
                  placeholder=""
                  type="2"
                  class="short-input"
                  ref="refDeduct"
                  v-model="queryContent.deductOperatorId">
                </sc-users>
              </li>
            </ul>
          </div>
          <div class="search-btn-box">
            <el-button type="danger" @click="resetQueryContent" icon="el-icon-refresh">重置</el-button>
            <el-button type="primary" @click="onSearch" icon="el-icon-search">查询</el-button>
          </div>
        </div>
      </div>
    </sc-expand-collapse>
    <div class="content-box">
      <div class="btn-box">
        <div>
          <el-button
            type="danger"
            :disabled="currentRow.length === 0"
            @click="openDeduct('批量扣款', 1)"
            v-if="z_method.showBtn('btn_rtiFee_deduct')">
            扣款
          </el-button>
          <el-button
            type="danger"
            :disabled="currentRow.length === 0"
            @click="openDeduct('批量线下收款', 2)"
            v-if="z_method.showBtn('btn_rtiFee_underLineDeduct')">
            线下收款
          </el-button>
          <el-button
            type="danger"
            :disabled="currentRow.length === 0"
            v-if="z_method.showBtn('btn_rtiFee_cancelDeduct')"
            @click="batchUpdate">
            取消扣款
          </el-button>
          <el-button
            type="success"
            :disabled="currentRow.length === 0"
            v-if="z_method.showBtn('btn_rtiFee_editUser')"
            @click="openEdit">
            编辑
          </el-button>
          <el-button
            type="success"
            :disabled="currentRow.length === 0"
            v-if="z_method.showBtn('btn_rtiFee_sendSettlement')"
            @click="batchUpdate">
            同步寄件结算对象
          </el-button>
          <el-button
            type="success"
            :disabled="currentRow.length === 0"
            v-if="z_method.showBtn('btn_rtiFee_paiSettlement')"
            @click="batchUpdate">
            同步派件结算对象
          </el-button>
          <el-button
            type="success"
            :disabled="currentRow.length === 0"
            v-if="z_method.showBtn('btn_rtiFee_userOrCustomer')"
            @click="batchUpdate">
            同步运单发放
          </el-button>
        </div>
        <el-button type="primary" icon="el-icon-download" @click="exportNew">导出</el-button>
      </div>
      <div :style="{height: contentHeight + 'px'}" @click="onClickTable">
        <Spread :ng-data="tableData" :headData="tableHead" @checkboxAllClick="checkboxAllClick"></Spread>
      </div>
      <div class="page-box">
        <sz-pagination
          :page="pagination.currentPage"
          :pageSize="pagination.pageSize"
          :total="pagination.total"
          @fetchData="fetchData"/>
      </div>
    </div>
    <!-- 导出 -->
    <csv
      :visible="exportObj.visible"
      :fileName="exportObj.fileName"
      :head="tableHead"
      :paramData="exportObj.excelData"
      :sum="exportObj.sum"
      :current="exportObj.current"
      @clear="closeDialog"
    />
    <async-export
      ref="asyncExport"
      :exportData="tableHead.columns"
      :exportName="exportObj.fileName"
      :exportUrl="exportUrl"
      :exportOption="optionsClone"
    />
    <sz-batch-process
      :title="batchTitle"
      :dialogVisible="batchDialogVisible"
      :columns="columns"
      :tableData="batchRow"
      :batchUrl="batchUrl"
      :data-validate="dataValidate"
      @model="close"
    />
    <!--扣款-->
    <el-dialog
      :title="deductForm.deductFeeType > 1 ? '单个线下收款' : '单个扣款'"
      v-drag-dialog
      width="400px"
      :close-on-click-modal="false"
      :modal-append-to-body="false"
      :visible.sync="deductDialogVisible">
      <div class="form-box">
        <div class="form-box-item">
          <span class="form-title">结算编号/单号：</span>
          <el-input
            :disabled="true"
            class="form-input"
            v-model="deductForm.fineCode"/>
        </div>
        <div class="form-box-item">
          <span class="form-title">结算对象：</span>
          <el-input
            :disabled="true"
            class="form-input"
            v-model="deductForm.accountUserName"/>
        </div>
        <div class="form-box-item">
          <span class="form-title">金额：</span>
          <el-input
            :disabled="true"
            class="form-input"
            v-model="deductForm.fineFee"/>
        </div>
        <div class="form-box-item">
          <span class="form-title">扣款金额：</span>
          <el-input
            class="form-input"
            v-model="deductForm.deductFee"/>
        </div>
        <div class="form-box-item">
          <span class="form-title">备注：</span>
          <el-input
            type="textarea"
            class="form-input"
            :rows="3"
            resize="none"
            maxlength="30"
            placeholder="最多输入30个字!"
            v-model="deductForm.remark"
          ></el-input>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="deductDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="singleDeduct()">确 定</el-button>
      </span>
    </el-dialog>
    <!--编辑-->
    <el-dialog
      title="编辑"
      v-drag-dialog
      width="400px"
      :modal-append-to-body="false"
      :close-on-click-modal="false"
      :visible.sync="editDialogVisible">
      <div class="form-box">
        <div class="form-box-item">
          <span class="form-title">关联业务员：</span>
          <sc-users
            class="form-input"
            placeholder=""
            type="2"
            popper-class="max-index"
            ref="editUsers"
            v-model="editUserId"/>
        </div>
      </div>
      <span slot="footer" class="dialog-footer">
        <el-button @click="editDialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="confirmEdit">确 定</el-button>
      </span>
    </el-dialog>
  </main>
</template>

<script>
  import { pagerMixin } from '@/utils/mixin';
  import { cloneDeep,trim } from 'lodash';
  import moment from 'moment'
  import { returnDetailAjax,returnDetailExport,returnDeductAjax,returnUnderlineDeductAjax } from '@/config/api'
  import { billUtils } from "@zto_module/shenzhou-components-static";
  export default {
    name: "detailBill",
    mixins: [pagerMixin],
    activated() {
      this.$nextTick(() => {
        this.z_method.refreshSpread()
      })
    },
    mounted() {
      this.$nextTick(() => {
        this.z_method.refreshSpread();
      });
    },
    data (){
      return {
        height: 342,
        billCode: '',
        codeType: true,
        resetBillCode: 0,
        feeType: [
          {
            label: '全部',
            id: -1
          },
          {
            label: '退件费',
            id: 1
          },
          {
            label: '转账费',
            id: 2
          },
          {
            label: '拦截费',
            id: 0
          }
        ],
        billType: [
          {
            label: '全部',
            id: -3
          },
          {
            label: '业务员',
            id: 4
          },
          {
            label: '承包区',
            id: 2
          },
          {
            label: '客户',
            id: 0
          },
          {
            label: '无对象',
            id: -1
          },
          {
            label: '下级网点',
            id: 99
          }
        ],
        deductStatus: [
          {
            label: '全部',
            id: -1
          },
          {
            label: '未扣款',
            id: 0
          },
          {
            label: '已线上扣款',
            id: 1
          },
          {
            label: '已线下收款',
            id: 2
          }
        ],
        queryContent: {
          startScanDate: new Date(moment().subtract(1, 'days').format('YYYY-MM-DD') + ' 00:00:00'),
          endScanDate: new Date(moment().subtract(1, 'days').format('YYYY-MM-DD') + ' 23:59:59'),
          fineWater: '',         //流水号
          fineCode: '',          //单号
          recManId: '',          //业务员
          fineType: -1,          //费用类型
          settlementType: -3,    //账单类型
          deductOperatorId: '',  //扣款人
          deductStatus: -1,      //扣款状态
          dateType: 0,          //时间类型 0结算时间 1业务发生时间
          accountUserId: '',     //结算对象
          accountUserType: '',
          customerId: '',        //客户
          salesManId: '',        //运单发放
          preSiteId: ''          //下级网点
        },
        optionsClone: {},
        tableHead: {
          rowStyle: function (row) {
            if(Number(row.deductStatus) === 1 || Number(row.deductStatus) === 2) return {foreColor: "green"}
          },
          columns: [
            {
              displayName: "选择",
              checked: true,
              name: "check",
              allCheck: true,
              isBillCode: true,
              cellType: new GC.Spread.Sheets.CellTypes.CheckBox(),
              isHideFilter: false,
              iShow: false,
              size: 50,
              isEdit: true,
              isCheckBox: true,
              onchange: (row, val) => {
                this.selectionSite(row, val);
              },
              dblclick: row => {
                this.selectionSite();
              }
            },
            {
              displayName: '结算编号/单号',
              name: 'fineCode',
              size: 150,
              isCode: true,
              dblclick: row => {
                window.open(this.z_param.billsUrl + row);
              }
            },
            {
              displayName: '结算流水号',
              name: 'fineWater',
              isCode: true,
              size: 150
            },
            {
              displayName: '中心结算时间',
              name: 'fineDate',
              size: 150,
              formatter: "yyyy-MM-dd hh:mm:ss"
            },
            {
              displayName: '业务发生时间',
              name: 'scanDate',
              size: 150,
              formatter: "yyyy-MM-dd hh:mm:ss"
            },
            {
              displayName: '费用类型',
              name: 'fineType',
              value: (row) => {
                let val = row['fineType'];
                if (val === 1)
                  return '退件费';
                if (val === 2)
                  return '转账费';
                if (val === 0)
                  return '拦截费';
                return '';
              }
            },
            {
              displayName: '账单类型',
              name: 'settlementType',
              value: (row) => {
                let val = row['settlementType'];
                if(val === -1){
                  return '无对象'
                }else if (val === 0)
                  return '客户';
                if (val === 2)
                  return '承包区';
                if (val === 4)
                  return '业务员';
                if(val === 99){
                  return '下级网点'
                }
                return '';
              }
            },
            {
              displayName: '客户',
              name: 'customerName',
            },
            {
              displayName: '客户编码',
              name: 'customerCode',
              isCode: true
            },
            {
              displayName: '业务员',
              name: 'recManName',
            },
            {
              displayName: '业务员工号',
              name: 'recManCode',
              isCode: true
            },
            /*{
              displayName: '下级网点',
              name: 'preSiteName'
            },
            {
              displayName: '下级网点编码',
              name: 'preSiteCode',
              isCode: true
            },*/
            {
              displayName: '结算对象',
              name: 'accountUserName',
            },
            {
              displayName: '结算对象编码',
              name: 'accountUserCode',
              isCode: true,
            },
            {
              displayName: '金额',
              name: 'fineFee',
              showTotal: true,
            },
            {
              displayName: '扣款金额',
              name: 'deductFee',
              showTotal: true,
            },
            {
              displayName: '扣款状态',
              name: 'deductStatus',
              value: (row) => {
                if(row.deductStatus === 0){
                  return '未扣款'
                }else if(row.deductStatus === 1){
                  return '已线上扣款'
                }else if(row.deductStatus === 2){
                  return '已线下收款'
                }
              }
            },
            {
              displayName: '扣款人',
              name: 'deductOperator',
            },
            {
              displayName: '扣款时间',
              name: 'deductDate',
              size: 150,
              formatter: "yyyy-MM-dd hh:mm:ss",
            },
            {
              displayName: '运单发放',
              name: 'salesManName',
            },
            {
              displayName: '备注',
              name: 'remark',
              size: 400,
              isConversionComma:true
            }
          ]
        },
        tableData: [],
        currentRow: [],
        batchRow: [],
        batchTitle: '',
        batchUrl: '',
        batchDialogVisible: false,
        columns: [],
        exportObj: {
          fileName: '收退件转账拦截费-明细结算'
        },
        exportUrl: returnDetailExport,
        deductDialogVisible: false,
        deductForm: {
          fineCode: '',
          accountUserName: '',
          fineFee: '',
          deductFee: '',
          remark: '',
          fineType: '',
          settlementType: '',
          id: '',
          deductFeeType: 0,
          isBatch: 0
        },
        editUserId: '',
        editDialogVisible: false,
        settleParam: {
          types: ''  , //0,2,4为自营，99为下级网点
        }
      }
    },
    methods: {
      summaryToDetail(row){
        this.resetQueryContent();
        this.queryContent.startScanDate = moment(row.accountDate).format('YYYY-MM') + '-01 00:00:00';
        this.queryContent.endScanDate = moment(new Date(moment(row.accountDate).add(1, 'M').format('YYYY-MM-DD HH:mm:ss')).getTime() - 1000).format('YYYY-MM-DD HH:mm:ss');
        this.queryContent.fineType = row.fineType;
        this.queryContent.settlementType = row.settlementType;
        this.queryContent.accountUserType = row.settlementType;
        if(row.settlementType === 99){
          this.settleParam.types = '99'
        }else {
          this.settleParam.types = '0,2,4'
        }
        setTimeout(() => {
          this.queryContent.accountUserId = row.accountUserId;
          this.onSearch()
        },0)
      },
      settlementChange(obj) {
        if(obj !== null){
          this.queryContent.accountUserType = obj.type;
        }else {
          this.queryContent.accountUserType = '';
        }
      },
      startDateChange(value) {
        if(value){
          this.$refs.timePickerB.focus()
        }
      },
      onSearch() {
        if(!!trim(this.billCode)){
          let bills = trim(this.billCode).split(/[,\r\n]/);
          if (bills.length > 1000) {
            let msg = '最多支持1000个' + (this.codeType ? '单' : '流水') + '号查询';
            this.msg(msg);
            return;
          }
          if(this.codeType){
            this.queryContent.fineCode = bills.join(',');
            this.queryContent.fineWater = '';
          }else {
            this.queryContent.fineWater = bills.join(',');
            this.queryContent.fineCode = '';
          }
        }else{
          this.queryContent.fineWater = '';
          this.queryContent.fineCode = '';
          if(!this.queryContent.startScanDate && !this.queryContent.endScanDate){
            this.msg('查询时间不能为空');
            return
          }
          if(this.queryContent.startScanDate || this.queryContent.endScanDate){
            if(this.timeCheck(this.queryContent.startScanDate,this.queryContent.endScanDate,this.queryContent.dateType===0 ? '结算时间' : '业务发生时间')) return;
            this.queryContent.startScanDate = moment(this.queryContent.startScanDate).format('YYYY-MM-DD HH:mm:ss');
            this.queryContent.endScanDate = moment(this.queryContent.endScanDate).format('YYYY-MM-DD HH:mm:ss');
          }
        }
        this.pagination.currentPage = 1;
        this.queryContent.pageSize = this.pagination.pageSize;
        this.queryContent.currentPage = this.pagination.currentPage;
        this.query(this.queryContent)
      },
      query(param) {
        if(param){
          this.optionsClone = cloneDeep(param);
        }
        returnDetailAjax(this.optionsClone).then(res => {
          this.settleParam.types = '';
          if(res.code === 0 && res.data.list && res.data.list.length > 0){
            this.tableData = res.data.list;
            this.tableData.map(item => {
              if(item.deductStatus === 0){
                item.deductOperator = '';
                item.deductDate = '';
              }
            });
            if(this.optionsClone.fineWater || this.optionsClone.fineCode) {
              this.pagination.total = 0;
            }else{
              this.pagination.total = res.data.totalRow;
            }
          }else if(res.code === 0){
            this.msg('暂无数据！');
            this.pagination.total = 0;
            this.tableData = [];
          }else {
            this.pagination.total = 0;
            this.tableData = [];
          }
        }).catch(res => {
          this.settleParam.types = '';
          this.pagination.total = 0;
          this.tableData = [];
        })
      },
      exportNew() {
        if(this.optionsClone.fineWater || this.optionsClone.fineCode){
          if(this.tableData.length > 0){
            this.checklistData = this.tableData;
            this.checklistExport()
          }else{
            this.msg('没有数据无法导出!')
          }
        }else{
          if(this.pagination.total > 800000){
            this.msg('最大导出不能超过80万');
            return
          }
          if(this.pagination.total > this.pagination.pageSize){
            this.$refs.asyncExport.export();
          }else{
            this.checklistData = this.tableData;
            this.checklistExport()
          }
        }
      },
      heightChange(val) {
        if(val){
          this.height = 342;
        }else {
          this.height = 220;
        }
        this.$nextTick(() => {
          this.z_method.refreshSpread();
        });
      },
      resetQueryContent() {
        this.billCode = '';
        this.resetBillCode++;
        this.codeType = true;
        this.queryContent = {
          startScanDate: new Date(moment().subtract(1, 'days').format('YYYY-MM-DD') + ' 00:00:00'),
          endScanDate: new Date(moment().subtract(1, 'days').format('YYYY-MM-DD') + ' 23:59:59'),
          fineWater: '',
          fineCode: '',
          recManId: '',
          fineType: -1,
          settlementType: -3,
          deductOperatorId: '',
          deductStatus: -1,
          dateType: 0,
          accountUserId: '',
          accountUserType: '',
          customerId: '',
          salesManId: '',
          preSiteId: ''
        };
        this.$refs.refSettlement.clear();
        this.$refs.refUser.clear();
        this.$refs.refCustomer.clear();
        this.$refs.refDeduct.clear();
        this.$refs.refUserOrCustomer.clear();
      },
      onClickTable() {
        this.$refs.refSettlement.autoBlur();
        this.$refs.refUser.autoBlur();
        this.$refs.refCustomer.autoBlur();
        this.$refs.refDeduct.autoBlur();
        this.$refs.refUserOrCustomer.autoBlur();
        this.$refs.timePickerA._data.pickerVisible = false;
        this.$refs.timePickerB._data.pickerVisible = false;
        if(this.$refs.timeSelect.visible) this.$refs.timeSelect.visible = false;
        if(this.$refs.feeType.visible) this.$refs.feeType.visible = false;
        if(this.$refs.billType.visible) this.$refs.billType.visible = false;
        if(this.$refs.deductStatus.visible) this.$refs.deductStatus.visible = false;
      },
      getType(value){
        this.codeType = value;
      },
      timeCheck(startDate, endDate, text){
        if(!startDate || !endDate){
          this.msg('请选择完整的' + text + '时间段');
          return true
        }
        if(moment(startDate).isAfter(endDate)){
          this.msg(text + '开始时间不能大于结束时间');
          return true
        }
        let timeInterval = moment(endDate).subtract(31, 'days');
        let isAfter = moment(timeInterval).isAfter(moment(startDate));
        if(isAfter) {
          this.msg(text + '相差不能大于31天');
          return true
        }
        return false
      },
      //全选和多选
      checkboxAllClick(val) {
        this.selectionSite([], val);
      },
      selectionSite(rows, row) {
        this.currentRow = this.tableData.filter(res => {
          return res.check === true;
        });
      },
      openDeduct(title, deductType) {
        if(this.currentRow.length === 1){
          this.deductForm = {
            fineCode: this.currentRow[0].fineCode,
            accountUserName: this.currentRow[0].accountUserName,
            fineFee: this.currentRow[0].fineFee,
            deductFee: cloneDeep(this.currentRow[0].fineFee),
            remark: cloneDeep(this.currentRow[0].remark),
            fineType: this.currentRow[0].fineType,
            settlementType: this.currentRow[0].settlementType,
            id: this.currentRow[0].id,
            deductFeeType: deductType,
            isBatch: 0
          };
          this.deductDialogVisible = true;
        }else if(this.currentRow.length > 1){
          this.batchTitle = title;
          this.deductForm.deductFeeType = deductType;
          this.batchUpdateAjax()
        }
      },
      checkNum(num){
        let status = false;
        if (num.toString().includes(".")) {
          let newArr = num.toString().split(".");
          let decL = newArr[1].length;
          if (decL > 2) {
            status = true;
          }
        }
        return status;
      },
      singleDeduct() {
        if(this.deductForm.deductFee !== 0 && this.deductForm.deductFee !== '0' && (this.deductForm.deductFee === '' || !this.deductForm.deductFee)){
          this.msg('扣款金额不能为空!');
          return
        }
        if(this.deductForm.deductFee == 0){
          this.msg('扣款金额不能为0!');
          return
        }
        if(!(/^\d+(\.\d+)?$/.test(this.deductForm.deductFee))){
          this.msg('请输入有效数字且最多能输入两位小数！');
          return
        }
        if(this.checkNum(this.deductForm.deductFee)){
          this.msg('扣款金额小数点只能保留2位小数!');
          return
        }
        if (Number(this.deductForm.deductFee) < 0 || Number(this.deductForm.deductFee) > 9999999) {
          this.msg("扣款金额的范围是0~9999999");
          return
        }
        if(this.deductForm.remark && !billUtils.regRemark(this.deductForm.remark)){
          this.msg('只能输入数字或字母或中文或常见符号!');
          return
        }
        if(this.deductForm.deductFeeType < 2){
          this.deductAjax()
        }else{
          this.underlineDeductAjax()
        }
      },
      deductAjax() {
        returnDeductAjax(this.deductForm).then(res => {
          if(res.code === 0){
            this.$message({
              message: '操作成功!',
              type: 'success'
            });
            this.query();
            this.deductDialogVisible = false;
            this.deductForm = {
              fineCode: '',
              accountUserName: '',
              fineFee: '',
              deductFee: '',
              remark: '',
              fineType: '',
              settlementType: '',
              id: '',
              deductFeeType: '',
              isBatch: 0
            }
          }
        })
      },
      underlineDeductAjax() {
        returnUnderlineDeductAjax(this.deductForm).then(res => {
          if(res.code === 0){
            this.$message({
              message: '操作成功!',
              type: 'success'
            });
            this.query();
            this.deductDialogVisible = false;
            this.deductForm = {
              fineCode: '',
              accountUserName: '',
              fineFee: '',
              deductFee: '',
              remark: '',
              fineType: '',
              settlementType: '',
              id: '',
              deductFeeType: '',
              isBatch: 0
            }
          }
        })
      },
      openEdit() {
        this.editUserId = '';
        if(this.currentRow.length > 0){
          if(this.$refs.editUsers){
            this.$refs.editUsers.clear()
          }
          this.editDialogVisible = true;
        }
      },
      confirmEdit() {
        if(!this.editUserId){
          this.msg('请选择业务员!');
          return
        }
        this.editDialogVisible = false;
        this.batchTitle = '编辑';
        this.batchUpdateAjax()
      },
      batchUpdate(e){
        if(this.currentRow.length === 0){
          this.msg('请选择要操作的数据!');
          return
        }
        this.batchTitle = e.target.innerText;
        this.batchUpdateAjax()
      },
      batchUpdateAjax() {
        if(this.batchTitle === '批量扣款' || this.batchTitle === '批量线下收款'){
          if(this.batchTitle === '批量扣款'){
            this.batchUrl = '/bills/financeFeeBill/deductMoney';
          }else if(this.batchTitle === '批量线下收款'){
            this.batchUrl = '/bills/financeFeeBill/underLineDeductMoney';
          }
          this.batchRow = this.currentRow.map(current => {
            return {
              id: current.id,
              fineCode: current.fineCode,
              fineType: current.fineType,
              settlementType: current.settlementType,
              deductFee: current.fineFee,
              deductFeeType: this.deductForm.deductFeeType,    //0未扣款 1线上扣款 2线下收款
              remark: current.remark,
              isBatch: 1,           //1是批量  0否
              accountUserName: current.accountUserName
            }
          })
        }else if(this.batchTitle === '取消扣款'){
          this.batchUrl = '/bills/financeFeeBill/deductMoney';
          this.batchRow = this.currentRow.map(current => {
            return {
              id: current.id,
              fineCode: current.fineCode,
              fineType: current.fineType,
              settlementType: current.settlementType,
              deductFeeType: 0,    //0未扣款 1线上扣款 2线下收款
              isBatch: 1           //1是批量  0否
            }
          })
        }else if(this.batchTitle === '编辑'){
          this.batchRow = this.currentRow.map(current => {
            return {
              id: current.id,
              fineCode: current.fineCode,
              userId: this.editUserId,
              settlementType: current.settlementType
            }
          })
        }else {
          this.batchRow = this.currentRow.map(current => {
            return {
              id: current.id,
              fineCode: current.fineCode,
              settlementType: current.settlementType
            }
          })
        }
        if(this.batchTitle === '编辑'){
          this.batchUrl = '/bills/financeFeeBill/updateRecUserId'
        }else if(this.batchTitle === '同步寄件结算对象'){
          this.batchUrl = '/bills/financeFeeBill/syncRecUserId'
        }else if(this.batchTitle === '同步派件结算对象'){
          this.batchUrl = '/bills/financeFeeBill/syncPaiUserId'
        }else if(this.batchTitle === '同步运单发放'){
          this.batchUrl = '/bills/financeFeeBill/syncBillSendUserId'
        }
        if(this.batchTitle === '批量扣款' || this.batchTitle === '批量线下收款') {
          this.columns = [
            {
              displayName: '结算编号/单号',
              name: 'fineCode',
              size: 100
            },
            {
              displayName: '结算对象',
              name: 'accountUserName',
              size: 100
            },
            {
              displayName: '扣款金额',
              name: 'deductFee',
              size: 50
            }
          ]
        }else {
          this.columns = [
            {
              displayName: '结算编号/单号',
              name: 'fineCode',
              size: 100
            }
          ]
        }
        this.batchDialogVisible = true;
      },
      dataValidate(row) {
        return new Promise((resolve, reject) => {
          if(row.settlementType === 99){
            reject('失败，不可处理下级账单!');
            return
          }
          resolve(row)
        })
      },
      close(val) {
        this.batchDialogVisible = false;
        this.batchRow = [];
        if(val !== "noSearch") {
          this.currentRow = [];
          this.query();
        }
      },
    },
    computed: {
      contentHeight() {
        if(this.z_param.isActiveH){
          return this.$store.getters.clientHeight - this.height + 60;
        }else{
          if(this.z_param.isNode){
            return this.$store.getters.clientHeight - this.height
          } else {
            return this.$store.getters.clientHeight - this.height + 60
          }
        }
      }
    }
  }
</script>

