<template>
  <main>
    <sc-expand-collapse @change="heightChange">
      <div slot="collapse">
        <div class="search-box">
          <ul class="search-input-box">
            <li>
              <span class="label">中心结算月份</span>
              <div class="datePicker">
                <el-date-picker
                  v-model="queryContent.accountDate"
                  :editable="false"
                  :clearable="false"
                  ref="timePicker"
                  type="month">
                </el-date-picker>
              </div>
            </li>
            <li>
              <span class="label">费用类型</span>
              <el-select
                v-model="queryContent.fineType"
                placeholder=""
                ref="feeType"
                class="short-select">
                <el-option
                  v-for="item in feeType"
                  :label="item.label"
                  :value="item.id"
                  :key="item.id"/>
              </el-select>
            </li>
            <li>
              <span class="label">账单类型</span>
              <el-select
                v-model="queryContent.settlementType"
                placeholder=""
                ref="billType"
                class="short-select">
                <el-option
                  v-for="item in billType"
                  :label="item.label"
                  :value="item.id"
                  :key="item.id"/>
              </el-select>
            </li>
            <li>
              <span class="label">结算对象</span>
              <sc-users
                type="7"
                placeholder=""
                class="short-input"
                ref="refSettlement"
                @change="settlementChange"
                v-model="queryContent.accountUserId">
              </sc-users>
            </li>
            <!--<li>
              <span class="label">下级网点</span>
              <sc-site
                v-model="queryContent.preSiteId"
                class="short-input"
                ref="refNextSite"
                placeholder=""
                :param="{child: true}" />
            </li>-->
          </ul>
          <div>
            <el-button type="primary" icon="el-icon-search" @click="onSearch">查询</el-button>
          </div>
        </div>
      </div>
    </sc-expand-collapse>
    <div class="content-box">
      <div class="btn-box">
        <el-button type="primary" icon="el-icon-download" @click="exportNew">导出</el-button>
      </div>
      <div :style="{height: contentHeight + 'px'}" @click="onClickTable">
        <Spread :ng-data="tableData" :head-data="tableHead"/>
      </div>
      <div class="page-box">
        <sz-pagination
          :page="pagination.currentPage"
          :pageSize="pagination.pageSize"
          :total="pagination.total"
          @fetchData="fetchData"/>
      </div>
    </div>
    <!-- 导出 -->
    <csv
      :visible="exportObj.visible"
      :fileName="exportObj.fileName"
      :head="tableHead"
      :paramData="exportObj.excelData"
      :sum="exportObj.sum"
      :current="exportObj.current"
      @clear="closeDialog"
    />
  </main>
</template>

<script>
  import { pagerMixin } from "../../../../utils/mixin";
  import moment from 'moment';
  import { cloneDeep } from 'lodash';
  import { returnSummaryUrl, returnSummaryAjax } from '@/config/api'
  export default {
    name: "monthSummary",
    mixins: [pagerMixin],
    activated() {
      this.$nextTick(() => {
        this.z_method.refreshSpread();
      });
    },
    mounted() {
      this.$nextTick(() => {
        this.z_method.refreshSpread();
      });
    },
    data (){
      return {
        height: 260,
        feeType: [{label: '全部', id: -1}, {label: '退件费', id: 1}, {label: '转账费', id: 2}, {label: '拦截费', id: 0}],
        billType: [{label: '全部', id: -3}, {label: '业务员', id: 4}, {label: '承包区', id: 2}, {label: '客户', id: 0}, {label: '无对象', id: -1}, {label: '下级网点', id: 99}],
        queryContent: {
          accountDate: new Date(),
          fineType: -1,
          settlementType: -3,
          accountUserId: '',
          accountUserType: '',
          preSiteId: ''
        },
        optionsClone: {},
        tableData: [],
        tableHead: {
          dblclick: this.dblclick,
          columns: [
            {
              displayName: '中心结算月份',
              name: 'accountDate',
              formatter: 'yyyy-MM',
              isCode: true
            },
            {
              displayName: '费用类型',
              name: 'fineType',
              value: (row) => {
                let val = row['fineType'];
                if (val === 1)
                  return '退件费';
                if (val === 2)
                  return '转账费';
                if (val === 0)
                  return '拦截费';
                return '';
              }
            },
            {
              displayName: '金额',
              name: 'fineFee',
              showTotal: true
            },
            {
              displayName: '结算对象',
              name: 'accountUserName'
            },
            {
              displayName: '结算对象编码',
              name: 'accountUserCode',
              isCode: true
            },
            {
              displayName: '账单类型',
              name: 'settlementType',
              value: (row) => {
                let val = row['settlementType'];
                if(val === -1){
                  return '无对象'
                }else if (val === 0)
                  return '客户';
                if (val === 2)
                  return '承包区';
                if (val === 4)
                  return '业务员';
                if(val === 99){
                  return '下级网点'
                }
                return '';
              }
            },
            {
              displayName: '应扣总金额',
              name: 'deductFee',
              showTotal: true
            },
            {
              displayName: '已扣款金额',
              name: 'deductedFee',
              showTotal: true
            },
            {
              displayName: '未扣款金额',
              name: 'notDeductFee',
              showTotal: true
            },
          ]
        },
        exportObj: {
          fileName: '收退件转账拦截费-月汇总'
        },
        requestUrl: returnSummaryUrl
      }
    },
    methods: {
      dblclick(v, row, args) {
        this.$emit('goto', 'tab1', row)
      },
      onSearch() {
        if(!this.queryContent.accountDate){
          this.msg('中心结算月份不能为空');
          return
        }
        this.queryContent.accountDate = moment(this.queryContent.accountDate).format('YYYY-MM');
        this.pagination.currentPage = 1;
        this.queryContent.pageSize = this.pagination.pageSize;
        this.queryContent.currentPage = this.pagination.currentPage;
        this.query(this.queryContent)
      },
      query(param) {
        if(param){
          this.optionsClone = cloneDeep(param);
        }
        returnSummaryAjax(this.optionsClone).then(res => {
          if(res.code === 0 && res.data.list && res.data.list.length > 0){
            this.pagination.total = res.data.totalRow;
            this.tableData = res.data.list;
          }else if(res.code === 0){
            this.msg('暂无数据！');
            this.pagination.total = 0;
            this.tableData = [];
          }else {
            this.pagination.total = 0;
            this.tableData = [];
          }
        }).catch(res => {
          this.pagination.total = 0;
          this.tableData = [];
        })
      },
      settlementChange(obj) {
        if(obj !== null){
          this.queryContent.accountUserType = obj.type;
        }else {
          this.queryContent.accountUserType = '';
        }
      },
      exportNew() {
        this.searchExport()
      },
      onClickTable() {
        this.$refs.timePicker._data.pickerVisible = false;
        if(this.$refs.feeType.visible) this.$refs.feeType.visible = false;
        if(this.$refs.billType.visible) this.$refs.billType.visible = false;
        this.$refs.refSettlement.autoBlur();
      },
      heightChange(val) {
        if(val){
          this.height = 260
        }else {
          this.height = 220
        }
        this.$nextTick(() => {
          this.z_method.refreshSpread()
        })
      },
    },
    computed: {
      contentHeight() {
        if(this.z_param.isActiveH){
          return this.$store.getters.clientHeight - this.height + 60;
        }else{
          if(this.z_param.isNode) {
            return this.$store.getters.clientHeight - this.height
          } else {
            return this.$store.getters.clientHeight - this.height + 60
          }
        }
      }
    }
  }
</script>

<style scoped lang="scss">
  .search-box{
    padding: 10px 10px 0;
    display: flex;
    justify-content: space-between;
    .search-input-box{
      display: flex;
      li{
        color: #595959;
        display: flex;
        align-items: center;
        .label{
          padding-right: 6px;
          padding-left: 10px;
        }
        .short-select{
          width: 110px;
        }
        .short-input{
          width: 150px;
        }
      }
    }
  }
</style>
